<template>
  <div>
    <van-nav-bar title="我的地址" left-text="返回" left-arrow @click-left="revert" />

    <div class="empty-tips" v-show="bool">
      <img src="https://jp.juancdn.com/jpwebapp_v1/images_v1/empty_icons/user_address.png?a98391eb-1&sv=449ce9ee" alt="">
      <p>还没有收货地址哦</p>
    </div>

    <van-address-list
      v-model="chosenAddressId"
      :list="list"
      @add="$router.push('/addAddress')"
      @edit="onEdit"
      @select="onSelect"
    />
  </div>
</template>

<script>
export default {
  data() {
    return {
      chosenAddressId: "1",
      list: [],
      bool:true
    };
  },
  methods: {
    revert() {
      this.$store.state.bool = false;
      this.$router.go(-1);
    },
    onEdit(item, index) {
      this.$store.state.address.modifyIndex = index;
      this.$router.push("/modifyAddress");
    },
    onSelect(con) {
      this.$store.state.address.list.forEach(item => {
        item.isDefault = false;
      });
      this.$store.state.address.list[con.id - 1].isDefault = true;
    }
  },

  created() {
    this.$store.state.bool = false;
    this.list = this.$store.state.address.list;
    let el = this.list.filter(item => {
      return item.isDefault;
    });
    if (el[0]) {
      this.chosenAddressId = el[0].id;
    } else {
      this.chosenAddressId = 1;
    }
    if (this.$store.state.address.list.length!=0) {
      // console.log(this.$store.state.address.list);
        this.bool = false
    }
  },
  // 生命周期钩子，最后销毁函数
  destroyed() {
    // 当组件被销毁时候，修改导航数据
    // this.$store.state.bool = true;
  }
};
</script>

<style lang="scss" scoped>
.empty-tips {
  font-size: 16px;
  background-color: #fff;
  box-sizing: border-box;
  color: #333;
  text-align: center;
  padding-top: 56px;
  padding-bottom: 50px;
  img{
        display: block;
    margin: 0 auto;
    margin-bottom: 12px;
    width: 140px;
    height: auto;
  }
}
</style>